* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

body {
  font-family: "Montserrat", sans-serif !important;
  font-style: normal;
  font-weight: normal;
  overflow-x: hidden;
  background: #f7f1e0;
  color: #302f34;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: auto;
  min-height: 100vh;
}
body:has(.burger-menu.active) {
  height: 100vh;
  overflow: hidden;
}

.hidden {
  overflow: hidden;
}

a {
  display: block;
  text-decoration: none !important;
  cursor: pointer;
  color: var(--Black);
}

img {
  display: block;
  height: auto;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
  margin-block-start: 0;
  margin-block-end: 0;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
  font-weight: bold;
}

ul,
ol {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

p {
  margin: 0;
  padding: 0;
}

#map {
  width: 100%;
  height: 100%;
}

button {
  border: none;
  cursor: pointer;
  background: none;
}

.title {
  font-weight: 500;
  font-size: 4.2317708333vw;
  line-height: 90%;
}
@media (max-width: 1024px) {
  .title {
    font-size: 3.90625vw;
  }
}
@media (max-width: 768px) {
  .title {
    font-size: 32px;
  }
}

.paragraph {
  font-weight: 500;
  font-size: 1.0416666667vw;
  line-height: 120%;
}
@media (max-width: 1024px) {
  .paragraph {
    font-size: 1.5625vw;
  }
}
@media (max-width: 768px) {
  .paragraph {
    font-size: 14px;
  }
}

/* Loader Styles */
.loading-overlay {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 70px;
  height: 70px;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 9999;
}

.loader {
  border: 10px solid #f3f3f3;
  border-top: 10px solid #fdc41f; /* Жовтий колір для верхньої половини */
  border-right: 10px solid #f3f3f3;
  border-bottom: 10px solid #fdc41f;
  border-radius: 50%;
  width: 70px;
  height: 70px;
  margin: auto;
  animation: spin 1.5s linear infinite;
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.2); /* Тінь для ефекту об'єму */
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(180deg); /* Поворот тільки на півкола */
  }
}
.btn {
  padding: 1.3020833333vw 1.6276041667vw;
  font-family: "Inter", sans-serif;
  font-weight: 400;
  font-size: 0.9114583333vw;
  line-height: 100%;
  color: #302f34;
  border: 0.0651041667vw solid #302f34;
  border-radius: 100px;
  transition: background 0.3s, border-color 0.3s, color 0.3s;
  cursor: pointer;
  white-space: nowrap;
}
@media (max-width: 1024px) {
  .btn {
    padding: 1.953125vw 2.44140625vw;
    font-size: 1.3671875vw;
    border-width: 0.09765625vw;
  }
}
@media (max-width: 768px) {
  .btn {
    padding: 14px 24px;
    font-size: 12px;
    border-width: 1px;
  }
}
.btn:hover {
  background: #f1e3be;
}
.btn.white {
  background: #fff;
  border-color: #fff;
  padding-right: 3.6458333333vw;
  position: relative;
  overflow: hidden;
}
@media (max-width: 1024px) {
  .btn.white {
    padding-right: 7.03125vw;
  }
}
@media (max-width: 768px) {
  .btn.white {
    padding-right: 72px;
  }
}
.btn.white::before {
  position: absolute;
  height: calc(100% - 0.5208333333vw);
  background: url("../svg/circle.svg") center/cover no-repeat;
  content: "";
  aspect-ratio: 1/1;
  right: 0.2604166667vw;
  top: 50%;
  transform: translateY(-50%);
  transition: transform 0.5s;
}
@media (max-width: 1024px) {
  .btn.white::before {
    right: 0.390625vw;
    height: calc(100% - 0.78125vw);
  }
}
@media (max-width: 768px) {
  .btn.white::before {
    right: 2px;
    height: calc(100% - 4px);
  }
}
.btn.white:hover {
  background: #f1e3be;
  border-color: #f1e3be;
}
.btn.white:hover::before {
  transform: translateY(-50%) rotate(45deg);
}
.btn.yellow {
  background: #fdc41f;
  border-color: #fdc41f;
  padding-right: 3.6458333333vw;
  position: relative;
  overflow: hidden;
}
@media (max-width: 1024px) {
  .btn.yellow {
    padding-right: 7.03125vw;
  }
}
@media (max-width: 768px) {
  .btn.yellow {
    padding-right: 72px;
  }
}
.btn.yellow::before {
  position: absolute;
  height: calc(100% - 0.5208333333vw);
  background: url("../svg/circle.svg") center/cover no-repeat;
  content: "";
  aspect-ratio: 1/1;
  right: 0.2604166667vw;
  top: 50%;
  transform: translateY(-50%);
  transition: transform 0.5s;
}
@media (max-width: 1024px) {
  .btn.yellow::before {
    right: 0.390625vw;
    height: calc(100% - 0.78125vw);
  }
}
@media (max-width: 768px) {
  .btn.yellow::before {
    right: 2px;
    height: calc(100% - 4px);
  }
}
.btn.yellow:hover {
  background: #eeb614;
  border-color: #eeb614;
}
.btn.yellow:hover::before {
  transform: translateY(-50%) rotate(45deg);
}

